const test = require('ava');
const request = require('supertest');
const server = require('../server');
const controller = require('../controllers');

var waitForPort = require('wait-for-port');

test.beforeEach(t => {
	waitForPort('localhost', 8080, function(err) {
	   server().start();
	});
});


function populateMultipleHosts()
{
	controller.setTestVals(
	   {
		   "hosts": [
		       {
				   "id": 1,
				   "name": "localhost.localdomain",
				   "ipv4": "127.0.0.1"
		       },
		       {
				   "id": 100,
				   "name": "localhost.hundredshire",
				   "ipv4": "127.10.10.1"
		       },
		       {
				   "id": 103,
				   "name": "google.com",
				   "ipv4": "172.217.3.216"
		       }
		  
		   ]
	   }
	);
}

test.only('Get a list of hosts', async t => {
    t.plan(5);	
	
	console.log("HERE0");
	waitForPort('localhost', 8080, function(err) {
			
		console.log("HERE1");
		server().start(function () {

			console.log("HERE2");
			populateMultipleHosts();

			
			request('http://localhost:8080')
				.get('/hosts')
				.set('Accept', 'application/json')
				.end(function(err, res){
					
					console.log("HERE3");
                  
					var hosts = JSON.parse(res.text);
					
					t.is(res.status, 200);
					t.is(hosts[0].id, 1);
					t.is(hosts[1].ipv4, "127.10.10.1");
					t.is(hosts[2].name, "google.com");
					t.is(hosts.length, 3);
				});
		});
	});
});

test('Get a single valid host', async t => {

	t.plan(4);

    populateMultipleHosts();
	
	const res = await request('http://localhost:8080')
	    .get('/host/100')
	    .set('Accept', 'application/json')
	
	var host = JSON.parse(res.text);
	
	t.is(res.status, 200);
	t.is(host.id, 100);
	t.is(host.ipv4, "127.10.10.1");
	t.is(host.name, "localhost.hundredshire");
});

test('Get a single invalid host', async t => {

	t.plan(1);

    populateMultipleHosts();
	
	const res = await request('http://localhost:8080')
	    .get('/host/2')
	    .set('Accept', 'application/json')
		
	var host = JSON.parse(res.text);
	
	t.is(res.status, 404);
});
